*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  font-family: "Arial", sans-serif;
  background-color: #ecf0f1;
}
.container h2 {
  color: #34495e;
  text-align: center;
}
.container form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.container form input,
.container form select,
.container form button {
  padding: 0.5rem;
  border: none;
  border-radius: 5px;
  font-family: "Arial", sans-serif;
}
.container form button {
  background-color: #34495e;
  color: white;
  cursor: pointer;
}
.container form button:hover {
  opacity: 0.9;
}
.container .left-section,
.container .right-section {
  width: 45%;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: white;
}
.container table {
  width: 100%;
  border-collapse: collapse;
}
.container table th,
.container table td {
  padding: 0.5rem;
  border: 1px solid #34495e;
}
.container table th {
  background-color: #34495e;
  color: white;
}

.containerRepre {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: #ecf0f1;
}
.containerRepre .section {
  width: 50%;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: white;
  border-radius: 10px;
}
.containerRepre .section h2 {
  color: #34495e;
  text-align: center;
  margin-bottom: 1rem;
}
.containerRepre .section form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.containerRepre .section form input,
.containerRepre .section form select,
.containerRepre .section form button {
  padding: 0.5rem;
  border: none;
  border-radius: 5px;
}
.containerRepre .section form button {
  background-color: #34495e;
  color: white;
  cursor: pointer;
}
.containerRepre .section form button:hover {
  opacity: 0.9;
}

.containerCurso {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: #ecf0f1;
}
.containerCurso .section {
  width: 50%;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: white;
  border-radius: 10px;
}
.containerCurso .section h2 {
  color: #34495e;
  text-align: center;
  margin-bottom: 1rem;
}
.containerCurso .section form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.containerCurso .section form input,
.containerCurso .section form select,
.containerCurso .section form button {
  padding: 0.5rem;
  border: none;
  border-radius: 5px;
}
.containerCurso .section form input {
  border: 1px solid #34495e;
}
.containerCurso .section form button {
  background-color: #34495e;
  color: white;
  cursor: pointer;
}
.containerCurso .section form button:hover {
  opacity: 0.9;
}

.containerLog {
  display: block;
  margin: 0 auto;
  height: 100vh;
}
.containerLog .section {
  display: flex;
  justify-content: center;
  height: auto;
  align-items: center;
  flex-direction: column;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 30%;
  margin: 0 auto;
  margin-top: 10%;
}
.containerLog h2 {
  text-align: center;
  margin-bottom: 20px;
}
.containerLog .inputGroup {
  margin-bottom: 15px;
}
.containerLog .inputGroup label {
  display: block;
  margin-bottom: 5px;
}
.containerLog .inputGroup input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.containerLog button {
  background-color: #3498db;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.containerLog button:hover {
  background-color: #2980b9;
}

.menu {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #34495e;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.2);
  overflow-x: hidden;
  transition: 0.3s;
  padding-top: 60px;
  background-color: #F1F1F1;
}
.menu a {
  padding: 10px 15px;
  text-decoration: none;
  font-size: 20px;
  color: #34495e;
  display: block;
  transition: 0.3s;
}
.menu a:hover {
  color: #f1f1f1;
  background-color: #34495e;
}
.menu .close-btn {
  position: absolute;
  top: 20px;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
  color: #34495e;
  cursor: pointer;
}
.menu .close-btn:hover {
  color: #f1f1f1;
}

#openMenu {
  font-size: 20px;
  padding: 10px 20px;
  background-color: #F1F1F1;
  color: #34495e;
  border: none;
  cursor: pointer;
}
#openMenu:hover {
  color: white;
  background-color: #22303d;
}

.menuD {
  background-color: #F1F1F1;
}